@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
   margin-top: 3rem;
   
   .campo{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;

    label{
        flex: 0 0 10rem
    }
    input{
        flex: 1;
        padding: 1rem;
        border: none;
        background-color: lighten(v.$gris, 40%);
    }
    input[type = 'submit']{
        flex: 0;
        width: 10%;
        padding: 1.28rem 1rem;
        margin-left: 1rem;
        border: none;
        background-color: lighten(v.$gris, 40%);
    }
    select{
        flex: 1;
        padding: 1rem;
        border: none;
        background-color: lighten(v.$gris, 40%);
    }

   }
}