@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones{
    margin-top: 2rem;

    @include m.tablet(){
        display: flex;
        justify-content: space-between;
    }

    a{
        display: block;
        text-align: center;
        margin-bottom: 1rem;
        color: v.$verde;
        &:last-of-type{
            margin-bottom: 0;
        }
    }
}