@use 'base/variables' as v;
@use 'base/mixins' as m;


.botones{
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet(){
        flex-direction: row;
    }
}

.boton{
    @include m.boton( v.$gris , v.$blanco);
    padding: 1rem 3rem;
    border-bottom: 1px solid v.$blanco;
    &:last-of-type{
        border-bottom: none;
    }

    @include m.tablet(){
        border-right: 2px solid v.$blanco;
        border-bottom: none;
        &:last-of-type{
            border-right: none;
        }
    }

}

.texto-info{
    color: v.$negro;
    font-size: 1.6rem;
}

.titulo-admin{
    text-align: center;
    margin: 2rem 0;
    font-size: 2.7rem;
}

.tabla{
    margin-top: 2rem;
    width: 100%;
    border-spacing: 0.25rem;
    
    thead{
        background-color: v.$grisOscuro;
        th{
            color: v.$blanco;
            padding: 1rem;
        }
    }

    tbody{
        td{
            color: v.$negro;
            text-align: center;
            border-bottom: 3px solid v.$grisOscuro;
            a{
                color: v.$gris;
            }
        }
    }

    .w100{
        width: 100%;
    }

    .boton-eliminar{
        @include m.boton_2(v.$rojo, block);
        margin: 0.5rem 0 0.5rem 0;
        width: 100%;
    }

    .boton-actualizar{
        @include m.boton_2(v.$naranja, block);
        margin: 0.5rem 0 0.5rem 0;
    }
}

.calendario{
    margin-top: 4rem;
}

.seleccion{
    padding: 2rem;
    border: 2px solid v.$gris;
}

.center{
    text-align: center;
}