*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 80rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.footer {
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 1.5rem;
  margin: 3rem 0 3rem 0;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
.login input[type=submit] {
  background-color: rgb(0, 120, 0);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #004500;
  cursor: pointer;
}

.mensaje {
  margin-top: 3rem;
}

.formulario {
  margin-top: 3rem;
}
.formulario .campo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}
.formulario .campo input[type=submit] {
  flex: 0;
  width: 10%;
  padding: 1.28rem 1rem;
  margin-left: 1rem;
  border: none;
  background-color: #d8dadf;
}
.formulario .campo select {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}

.acciones {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  color: rgb(0, 120, 0);
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alertas {
  padding: 1rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 1rem 0;
  font-size: 1.4rem;
  max-width: 80rem;
}
.alertas.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alertas.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.nombre,
.lema {
  text-align: center;
}

.nombre {
  color: rgb(0, 120, 0);
  margin: 2rem 0 1rem 0;
}

.lema {
  color: #000;
  margin: 0 0 2rem 0;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(0, 120, 0);
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-around;
  }
}
.navegacion a {
  padding: 1.5rem 6rem;
  margin-bottom: 0.2rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  border-bottom: 2px solid #FFFFFF;
}
.navegacion a:last-of-type {
  border-bottom: none;
}
@media (min-width: 768px) {
  .navegacion a {
    padding: 1.5rem 6rem;
    margin-bottom: 0;
    border-bottom: none;
    border-right: 2px solid #FFFFFF;
  }
  .navegacion a:last-of-type {
    border-right: none;
  }
}

.botones {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .botones {
    flex-direction: row;
  }
}

.boton {
  background-color: #6b7280;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem 3rem;
  border-bottom: 1px solid #FFFFFF;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #545964;
  cursor: pointer;
}
.boton:last-of-type {
  border-bottom: none;
}
@media (min-width: 768px) {
  .boton {
    border-right: 2px solid #FFFFFF;
    border-bottom: none;
  }
  .boton:last-of-type {
    border-right: none;
  }
}

.texto-info {
  color: #000;
  font-size: 1.6rem;
}

.titulo-admin {
  text-align: center;
  margin: 2rem 0;
  font-size: 2.7rem;
}

.tabla {
  margin-top: 2rem;
  width: 100%;
  border-spacing: 0.25rem;
}
.tabla thead {
  background-color: #4B5563;
}
.tabla thead th {
  color: #FFFFFF;
  padding: 1rem;
}
.tabla tbody td {
  color: #000;
  text-align: center;
  border-bottom: 3px solid #4B5563;
}
.tabla tbody td a {
  color: #6b7280;
}
.tabla .w100 {
  width: 100%;
}
.tabla .boton-eliminar {
  background-color: rgb(169, 0, 0);
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  text-transform: uppercase;
  border: none;
  margin: 0.5rem 0 0.5rem 0;
  width: 100%;
}
.tabla .boton-eliminar:hover {
  background-color: #900000;
  cursor: pointer;
}
.tabla .boton-actualizar {
  background-color: #F59E0B;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  text-transform: uppercase;
  border: none;
  margin: 0.5rem 0 0.5rem 0;
}
.tabla .boton-actualizar:hover {
  background-color: #dd8e09;
  cursor: pointer;
}

.calendario {
  margin-top: 4rem;
}

.seleccion {
  padding: 2rem;
  border: 2px solid #6b7280;
}

.center {
  text-align: center;
}