@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: v.$verde;

    @include m.tablet(){
        flex-direction: row;
        justify-content: space-around;
    }

    a{
        padding: 1.5rem 6rem;
        margin-bottom: 0.2rem;
        color: v.$blanco;
        font-size: 1.6rem;
        font-weight: 700;
        border-bottom: 2px solid v.$blanco;
        &:last-of-type{
            border-bottom: none;
        }

        @include m.tablet(){
            padding: 1.5rem 6rem;
            margin-bottom: 0;
            border-bottom: none;
            border-right: 2px solid v.$blanco;
            &:last-of-type{
                border-right: none;
            }
        }
    }

    
}